/* Scrolling class style */

.scrollable{
  overflow-y: scroll;
  overflow-x: hidden;
}

.my-food.scrollable {
  overflow-y: hidden;
  overflow-x: scroll;
}

.scrollable::-webkit-scrollbar {
    width: 5px;
}


.scrollable::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 8px rgba(0,0,0,0.3);
    border-radius: 10px;
}

.scrollable::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}


/* numberLine class style */


.line-container {
  position: relatives;
  margin-top: 10px; margin-bottom: 10px;
  overflow-x: hidden;
}

.numberline {
  position: relative;
  border-color: black; border-width: 1px; border-style: solid;
  height: 15px;
  background-color: white;
}

.scale-origin, .scale-end {
  position: absolute;
  top: 10px;
}

.scale-end {
  right: 0;
}

.line {
  position: absolute;
  width: 30%;
  height: 100%;
  background-color: brown;
  opacity: .8;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
}


.threshold-1, .threshold-2, .target {
  position: absolute;
  height: 15px;
  width: 2px;
  background-color: black;
}

.threshold-1 p, .threshold-2 p, .target p {
  position: absolute;
  bottom: 10px;
}

.threshold-1 {
  left: 25%;
}

.threshold-2 {
  left: 50%;
}

/* Animations */

/* Progress bar stars animations in and out */
@-webkit-keyframes fadeIn{
    0%{
        -webkit-transform:  rotate(0deg) translateX(0) translateY(-50px);
                transform:  rotate(0deg) translateX(0) translateY(-50px);
        opacity: 0;
        }
    100%{
        -webkit-transform: rotate(360deg) scale(1) translateX(0) translateY(0);
                transform: rotate(360deg) scale(1) translateX(0) translateY(0);
        opacity: 1;
        }
}
@keyframes fadeIn{
    0%{
        -webkit-transform:  rotate(0deg) translateX(0) translateY(-50px);
                transform:  rotate(0deg) translateX(0) translateY(-50px);
        opacity: 0;
        }
    100%{
        -webkit-transform: rotate(360deg) scale(1) translateX(0) translateY(0);
                transform: rotate(360deg) scale(1) translateX(0) translateY(0);
        opacity: 1;
        }
}



@-webkit-keyframes fadeOut{
    0%{
        -webkit-transform:  rotate(0deg) translateX(0) translateY(0);
                transform:  rotate(0deg) translateX(0) translateY(0);
        opacity: 1;
        }
    100%{
        -webkit-transform: rotate(360deg) scale(1) translateX(0) translateY(-50px);
                transform: rotate(360deg) scale(1) translateX(0) translateY(-50px);
        opacity: 0;
        }
}



@keyframes fadeOut{
    0%{
        -webkit-transform:  rotate(0deg) translateX(0) translateY(0);
                transform:  rotate(0deg) translateX(0) translateY(0);
        opacity: 1;
        }
    100%{
        -webkit-transform: rotate(360deg) scale(1) translateX(0) translateY(-50px);
                transform: rotate(360deg) scale(1) translateX(0) translateY(-50px);
        opacity: 0;
        }
}



.fade-in{
    -webkit-animation: fadeIn .5s 1 ease;
            animation: fadeIn .5s 1 ease;
}

.fade-out{
  -webkit-animation: fadeOut .5s 1 ease;
          animation: fadeOut .5s 1 ease;
}



/* animations for landing*/

.fade-appear{
  opacity: 0;
}

.fade-appear.fade-appear-active{
  opacity: 1;
  -webkit-transition: all 1000ms ease;
  -o-transition: all 1000ms ease;
  transition: all 1000ms ease;
}

.fade-enter{
  opacity: 0;
}

.fade-enter.fade-enter-active{
  opacity: 1;
  -webkit-transition: all 1000ms ease;
  -o-transition: all 1000ms ease;
  transition: all 1000ms ease;
}


.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active{
  opacity: 0;
  -webkit-transition: all 1000ms ease;
  -o-transition: all 1000ms ease;
  transition: all 1000ms ease;
}

.fade-exit-done{
  opacity: 0;
}



/* fade-fast */
.fade-fast-appear, .fade-fast-enter{
  opacity: 0;
}

.fade-fast-appear.fade-fast-appear-active, .fade-fast-enter.fade-fast-enter-active {
  opacity: 1;
  -webkit-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
}

.fade-fast-exit {
  opacity: 1;
}

.fade-fast-exit-active, .fade-fast-exit-done {
  opacity: 0; -webkit-transition: all 200ms ease; -o-transition: all 200ms ease; transition: all 200ms ease;
}


/* grow */
.grow-appear, .grow-enter{
  opacity: 0;
  -webkit-transform: scaleX(0%) scaleY(0%);
      -ms-transform: scaleX(0%) scaleY(0%);
          transform: scaleX(0%) scaleY(0%);
}

.grow-appear.grow-appear-active, .grow-enter.grow-enter-active {
  opacity: 1;
  -webkit-transform: scaleX(100%) scaleY(100%);
      -ms-transform: scaleX(100%) scaleY(100%);
          transform: scaleX(100%) scaleY(100%);
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}


.enter-above-appear{
  opacity: 0;
  -webkit-transform: translateY(-100px);
      -ms-transform: translateY(-100px);
          transform: translateY(-100px);
}

.enter-above-appear-active{
  opacity: 1;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 1000ms ease-out;
  -o-transition: all 1000ms ease-out;
  transition: all 1000ms ease-out;
}



